import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class AuthService {
  authToken: any;
  user: any;

  constructor(private http: HttpClient) { }

  registerUser(user) {
    const headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/register', user, {headers});
  }

  authenticateUser(user) {
    const headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/authenticate', user, {headers});
  }

  getProfile(): any {
    if (this.authToken == null) { this.loadToken(); }
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        // tslint:disable-next-line:object-literal-key-quotes
        'Authorization': this.authToken
      })
    };
    console.log(httpOptions);
    return this.http.get('users/profile', httpOptions);
  }

  storeUserData(token, user) {
    localStorage.setItem('token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loadToken() { this.authToken = localStorage.getItem('token'); }

  isLoggedIn() {
    return (localStorage.getItem('token') != null);
  }

  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }
}
